import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import '../Login/Login.css'
import { auth, provider } from '../Firebase/Firebase'
import { createUserWithEmailAndPassword, signInWithPopup } from 'firebase/auth'
import { useNavigate } from "react-router-dom";
function SignUp() {
    const navigate = useNavigate();
    const [value, setvalue] = useState('')
    const [email, setemail] = useState('')
    const [password, setpassword] = useState('')
    const [userName, setusername] = useState('')
    const [error, seterror] = useState('')
    const [loading, setloading] = useState(false)

    const handleClick = () => {
        signInWithPopup(auth, provider).then((data) => {
            setvalue(data.user.email);
            localStorage.setItem('email', data.user.email);
            console.log(data)
            navigate("/");
        })
        
    }

    const handleRegister = (e) => {
        e.preventDefault();
        seterror('')
        if (email === '' || password === '' || userName === '') {
            seterror('Required field is empty')
        } else {
            createUserWithEmailAndPassword(auth, email, password).then((res) => {
                console.log(res)
                navigate("/");
                setloading(false)
            }).catch((err) => {
                console.log(err)
                seterror(err.message)
                setloading(false)
            })
        }
    }
    return (
        <>
            <div className="login">
                <div className="login-body">
                    <div className="logo">
                        <Link to='/'>
                            <img src="https://stackoverflow.design/assets/img/logos/so/logo-stackoverflow.svg" alt="Logo" width='140px' />
                        </Link>
                    </div>
                    <div className="google-login " onClick={handleClick}>
                        <p className="google-log"><img src='https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/2048px-Google_%22G%22_Logo.svg.png' alt='google'></img> Sign up with google</p>
                    </div>
                    <form>
                    <div className="logs">
                        <div className="username">
                            <p><b>Username</b></p>
                            <input type='text' value={userName} onChange={(e) => setusername(e.target.value)}></input>
                        </div>
                        <div className="email">
                            <p><b>Email</b></p>
                            <input type='text' value={email} onChange={(e) => setemail(e.target.value)}></input>
                        </div>
                        <div className="pass">
                            <p><b>Password</b></p>
                            <input type='password' value={password} onChange={(e) => setpassword(e.target.value)}></input>

                            <button className="log-btn" onClick={handleRegister}>{loading ? 'Sign in..' :'Sign Up'}</button>

                        </div>
                        <div className="err-msg" color='red'>{error}</div>

                    </div>
                    </form>  
                    <p>Already have an account? <Link to='/login' >Log in</Link></p>
                </div>

            </div>
        </>
    )
}

export default SignUp